<script>

import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'

import Popup from '@/components/Popup.vue'
import Fab from '@/components/Fab.vue'
import Header from '@/components/Header.vue'

export default {

  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Basic

  name: 'Restaurant',

  components: {
    Popup,
    Fab,
    Header,
  },




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Data/Props/Emits

  data() {
    return {
      $menuBtn: undefined,

      isPopupClose: true,
      popupType: 'info',
    }
  },




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Lifecycle Hooks

  mounted() {
    $('.f8-icon').f8icon();
    $('.f8-switch-daylight').f8switchdaylight();
    $('.m-restaurant-menu__btn').f8toggle();


    // Keep only one menu accordian open at a time
    this.$menuBtn = $('.m-restaurant-menu__btn');

    this.$menuBtn.on('opened.f8toggle', e => {
      this.$menuBtn.not(e.target).f8toggle('close');
    });
  },

  beforeUnmount() {
    $('.f8-icon').f8icon('destroy', true);
    $('.f8-switch-daylight').f8switchdaylight('destroy', true);
    $('.m-restaurant-menu__btn').f8toggle('destroy', true);


    this.$menuBtn.off('opened.f8toggle');
    this.$menuBtn = undefined;
  },




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Computed

  computed: {
    currRestaurant() {
      return this.getRestaurantById(this.$route.params.restaurant);
    },
    popupPayload() {
      return {
        logo: this.currRestaurant.logo,
        title: this.currRestaurant.title,
        cats: this.currRestaurant.cats,
      }
    },
    headerPayload() {
      return {
        title: this.currRestaurant.title,
        cats: this.currRestaurant.cats,
        isSleeping: this.currRestaurant.isSleeping,
      }
    },
    ...mapGetters('restaurants', [
      'getRestaurantById',
    ]),
  },




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Methods

  methods: {
    openPopup(type) {
      this.popupType = type;
      this.isPopupClose = false;
    },
  },

}

</script>








